body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.m-item{
    color: #fff
}
.ant-layout-header{
    background: inherit
}
.ant-menu-item{
    display: flex;
    align-items: center;
}
.m-head{
    height: 100%;
    display: flex;
    justify-content: flex-end;
    padding: 0 100px;
}
.m-head .u-item{
    display: flex;
    align-items: center;
}
.g-header{
    display: flex;
    padding: 0;
    background: #fff;
    border-bottom: 1px solid #ededed;
}
.g-header .m-head{
    flex-grow: 1;
    border: none
}

.g-header .m-title{
    padding-left: 100px;
}

@media screen and (max-width: 414px) {
    .u-logo{
        display: none
    }
    .m-head{
        padding:  0;
        justify-content: center;
    }
}
.m-main{
    height: calc( 100vh - 64px);
    width: 960px;
    margin: 0 auto;
    padding: 20px 0 0 0;
    background: #fff;
    overflow-y: scroll;
}
@media screen and (max-width: 414px) {
    .m-main{
        width: 100%;
        height: auto;
    }
}
.m-body{
    padding: 30px;
}


.m-item{
    height: 48px;
    line-height: 48px;
    color: #333;
    padding: 0 20px;
    border-bottom: 1px solid #ededed;
    cursor: pointer;
    -webkit-transition: background .3s;
    transition: background .3s;
    background: #fff;
}
.m-item:hover{
    background: #ededed
}
.m-item:active{
    background: #999
}
.m-title{
    text-align: left;
}
@media screen and (max-width: 414px) {
    .m-date{
        display: none
    }
    .m-item{
        height: auto;
    }
}
